import React from 'react';
import { Card,Button,notification,Select } from 'antd';
import './ui.less';

export default class Notification extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            size: 'default',
            loading:true,
        }
    }

    handleOpen = (type) => {
        notification[type]({
            message: 'title',
            description: 'desc',
            duration:3,//3s 4.5snull
            
        })
    }

    

    render() {
        const options = ['topLeft', 'topRight', 'bottomLeft', 'bottomRight'];

        return (
            <div>
                <Card title="" className="card-wrap">
                    <Button type="primary" onClick={()=>this.handleOpen('success')}>Success</Button>
                    <Button type="primary" onClick={()=>this.handleOpen('info')}>Info</Button>
                    <Button type="primary" onClick={()=>this.handleOpen('warning')}>Warning</Button>
                    <Button type="primary" onClick={()=>this.handleOpen('error')}>Error</Button>
                </Card>
                <Card title="-" className="card-wrap">
                    <Select
                        defaultValue="topRight"
                        style={{ width: 140, marginRight: 10 }}
                        onChange={(val) => {
                            notification.open({
                                type:'info',
                                message:`${val} title`,
                                description: '',
                                placement: val,//
                            });
                        }}
                    >
                        {options.map(val => 
                            <Select.Option key={val} value={val}>
                                {val}
                            </Select.Option>
                        )}
                    </Select>
                </Card>
            </div>
        )
    }

    


}


