import React from 'react';
import { Input,Button,List} from 'antd';


//UI-
const TodoListUI = (props =>
    <div>
        <div style={{ width: 400 }}>
            <Input value={props.inputVal} onChange={props.handleInputChange} style={{ width: 300 }} placeholder="item" />
            <Button onClick={props.handleBtnAdd} type="primary"></Button>
        </div>
        <List
            style={{ width: 300 }}
            bordered
            dataSource={props.list}
            renderItem={(item, index) => (<List.Item onClick={() => props.handleItemDelete(index)} key={index}>{item}</List.Item>)}
        />
    </div>);


export default TodoListUI;