import React from 'react';
import { Card, Form, Select, Button, Table, message, Modal, Radio, notification, DatePicker } from 'antd';
import axios from '../../axios/axios';
import Utils from './../../utils/utils';
import moment from 'moment';

const FormItem = Form.Item;
const Option = Select.Option;
const cityIdArr = [
    { id: '', name: '' },
    { id: 1, name: '' },
    { id: 2, name: '' },
    { id: 3, name: '' },
    { id: 4, name: '' },
    { id: 5, name: '' }
]
const orderStatusArr = [
    { id: '', name: '' },
    { id: 1, name: '' },
    { id: 2, name: '()' },
    { id: 3, name: '' }
]


export default class Order extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            list: [],//
            pagination:()=>{},//
            selectedRowKeys: [],//index [0]
            selectedItem: {},//
            isShowFinishOrderModal: false,//
            orderInfo: {},//
            finishOrderBtnDisabled: true,
            orderDetailBtnDisabled:true,
        }
    }

    params = {
        page: 1,
        pageSize: 10,
    }


    //-
    search = (postData) => {
        postData.startTime = moment(postData.startTime).unix();
        postData.endTime = moment(postData.endTime).unix();
        console.log('postData', postData)
        message.info(`${JSON.stringify(postData)}`)
    }

    componentDidMount() {
        this.getList();
    }

    onRowClick = (item,index) => {
        this.setState({
            selectedRowKeys: [index],
            selectedItem: item,
            finishOrderBtnDisabled: false,
            orderDetailBtnDisabled: false,
        })
    }

    //
    orderDetail = () => {
        let orderId = this.state.selectedItem.id;
        window.open(`/#/common/order/detail/${orderId}`,'_blank')
    }

    // 
    finishOrder = () => {
        let item = this.state.selectedItem;
       
        // if (Object.keys(item).length === 0) {
        //     //constructorstateselectedItem{}
        //     Modal.info({
        //         title: '',
        //         content:''
        //     })
        //     return;
        // }
        this.getOrderInfo(item);
    }

    //
    onFinishOrderSubmit = () => {
        axios.ajax({
            url: '/order/finish',
            method: 'post',
            data: {
                orderId:this.state.selectedItem.id
            }
        }).then((res) => {
            if (res.code === 0) {
                message.success('');
                this.setState({
                    isShowFinishOrderModal:false
                })
                this.getList();
                this.setState({
                    selectedRowKeys: [],//
                    selectedItem: {},//
                    finishOrderBtnDisabled: true,
                    orderDetailBtnDisabled: true,
                    
                })
            }
        })
    }

    //
    getOrderInfo = (item) => {
        axios.ajax({
            url: '/order/finish/info',
            data: {
                orderId:item.orderId
            }
        }).then((res) => {
            if (res.code === 0) {
                this.setState({
                    isShowFinishOrderModal:true,
                    orderInfo:res.result
                })
            }
        })
    }

    getList = () => {
        axios.ajax({
            url: '/order/list',
            data: {
                ...this.params
            }
        }).then((res) => {
            let _this = this;
            if (res.code === 0) {
                let list = res.result.list.map((item, index) => {
                    item.key = index;
                    return item;
                })
                this.setState({
                    list,
                    pagination:Utils.pagination(res,(current)=>{
                        _this.params.page = current;
                        _this.getList();
                    })
                })
            }
        })
    }

   

    render() {
        const columns = [
            {
                title: '',
                dataIndex: 'orderSn',
            },
            {
                title: '',
                dataIndex: 'bikeSn',
            },
            {
                title: '',
                dataIndex: 'userName',
            },
            {
                title: '',
                dataIndex: 'mobile',
            },
            {
                title: '',
                dataIndex: 'distance',
                render: (distance) => {
                    return distance/1000 + 'km'
                }
            },
            {
                title: '',
                dataIndex: 'totalTime',
            },
            {
                title: '',
                dataIndex: 'status',
                render: (status) => {
                    return status === 1?'':''
                }
            },
            {
                title: '',
                dataIndex:'startTime',
            },
            {
                title: '',
                dataIndex: 'endTime',
            },
            {
                title: '',
                dataIndex: 'totalFee',
            },
            {
                title: '',
                dataIndex: 'userPay',
            }
        ]
        columns.map(item => item.align = "center");
        const selectedRowKeys = this.state.selectedRowKeys;
        const rowSelection = {
            type: 'radio',
            selectedRowKeys
        }
        const formItemLayout = {
            labelCol:{span:5},
            wrapperCol:{span:19}
        }

        return (
            <div>
                <Card>
                    <FilterForm search={this.search} />
                </Card>
                <Card style={{marginTop:20}}>
                    <Button type="primary" disabled={this.state.orderDetailBtnDisabled} onClick={this.orderDetail}></Button>
                    <Button type="primary" disabled={this.state.finishOrderBtnDisabled} style={{marginLeft:10}} onClick={this.finishOrder}></Button>
                </Card>
                <div className="content-wrap">
                    <Table
                        bordered
                        columns={columns}
                        dataSource={this.state.list}
                        pagination={this.state.pagination}
                        rowSelection={rowSelection}
                        onRow={(item,index) => {
                            return {
                                onClick: () => {
                                    this.onRowClick(item,index);
                                }
                            }
                        }}
                    />
                </div>
                <Modal
                    title=""
                    width={600}
                    visible={this.state.isShowFinishOrderModal}
                    onCancel={() => {
                        this.setState({
                            isShowFinishOrderModal:false,
                        })
                    }}
                    onOk={this.onFinishOrderSubmit}
                >
                    <Form layout="horizontal">
                        <FormItem label="" {...formItemLayout}>
                            {this.state.orderInfo.bikeSn}
                        </FormItem>
                        <FormItem label="" {...formItemLayout}>
                            {this.state.orderInfo.battery + '%'}
                        </FormItem>
                        <FormItem label="" {...formItemLayout}>
                            {this.state.orderInfo.startTime}
                        </FormItem>
                        <FormItem label="" {...formItemLayout}>
                            {this.state.orderInfo.location}
                        </FormItem>
                    </Form>
                </Modal>
            </div>
        )
    }
}



class FilterForm extends React.Component {

    constructor(props) {
        super(props);
    }

    //
    search = (e) => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                console.log(values)
                this.props.search(values);
            }
        })
    }


    render() {
        const { getFieldDecorator } = this.props.form;
        return (
            <Form layout="inline">
                <FormItem label="">
                    {
                        getFieldDecorator('city', {
                            initialValue: '',
                        })(
                            <Select style={{ width: 135 }}>
                                {
                                    cityIdArr.map((item, index) => {
                                        return <Option value={item.id} key={item.id}>{item.name}</Option>
                                    })
                                }

                            </Select>
                        )
                    }
                </FormItem>
                <FormItem>
                    {
                        getFieldDecorator('startTime', {
                            initialValue:null
                        })(
                            <DatePicker showTime={true} style={{ width: 200 }} format="YYYY-MM-DD HH:mm:ss" placeholder="" />
                        )
                    }
                </FormItem>
                <FormItem label="~" colon={false} >
                    {
                        getFieldDecorator('endTime', {
                            initialValue:null
                        })(
                            <DatePicker showTime={true} style={{ width: 200 }} format="YYYY-MM-DD HH:mm:ss" placeholder="" />
                        )
                    }
                </FormItem>

                <FormItem label="">
                    {
                        getFieldDecorator('orderStatus', {
                            initialValue: '',
                        })(
                            <Select style={{ width: 135 }}>
                                {
                                    orderStatusArr.map((item, index) =>
                                        <Option value={item.id} key={item.id}>{item.name}</Option>
                                    )
                                }

                            </Select>
                        )
                    }
                </FormItem>

                <FormItem>
                    <Button onClick={this.search} type="primary" icon="search" style={{ margin: '0 10px' }}></Button>
                    <Button onClick={() => this.props.form.resetFields()}></Button>
                </FormItem>

            </Form>
        )
    }
}
FilterForm = Form.create({})(FilterForm);
