import React from 'react';
import { Card, Table, Avatar,Badge,Tooltip, Button,Radio,Modal,notification }from 'antd';
import moment from 'moment';
import axios from './../../axios/axios';
import Utils from './../../utils/utils';

export default class BasicTable extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            dataSource: [],
            selectedRowKeys: [],
            loading: false,
            actionMode: 'radio',
        }
    }

    params = {
        page: 1,
        pageSize:10
    }

    onChangeBtn = (e)=>{
        this.setState({
            actionMode: e.target.value,
            selectedRowKeys: [],//
            loading: false,
        })
    }

    //
    handleDelete = () => {
        Modal.confirm({
            title: '',
            content: '',
            okText: '',
            cancelText: '',
            onOk:()=> {
                console.log(this.state.selectedRowKeys);
                console.log(this.state.dataSource);
                notification.success({
                    message: '',
                    description: '',
                    duration:3,//3s 4.5snull
                })
            },
        })
    }

    onRowClick = (record,index) => {
        console.log('onRowClick->record',record);
        console.log('onRowClick->index', index);
        const selectedRowKeys = [index];
        this.setState({
            selectedRowKeys,
            selectedItem:record
        });
    }

   

    onSelectChange = (selectedRowKeys,selectedRows) => {
        console.log(selectedRowKeys);
        console.log('selectedRows',selectedRows);
        this.setState({
            selectedRowKeys,
            selectedRows
        });
    }

    getList = () => {
        axios.ajax({
            url: '/user/list',
            params: this.params,
        }).then((res) => {
            console.log('res',res);
            let _this = this;
            if (res.code === 0) {
                res.result.list.map((item, index) => {
                    item.key = index;
                })
                console.log(res)
                this.setState({
                    dataSource:res.result.list,
                    selectedRowKeys:[],
                    selectedRows:null,
                    pagination: Utils.pagination(res, (page, pageSize) => {
                        _this.params.page = page;
                        _this.params.pageSize = pageSize;
                        this.getList();
                    })
                })
            }
        })
    }

    //-->render--->-Ajax
    componentDidMount() {
        this.getList();
    }



    render() {
        const columns = [
            {
                title:'id',
                key:'id',
                dataIndex:'id'
            },
            {
                title:'',
                key:'userName',
                dataIndex:'userName'
            },
            {
                title:'',
                key:'userImg',
                dataIndex: 'userImg',
                render : (userImg) => {
                    return userImg?<Avatar src={userImg}/>:<Avatar style={{ backgroundColor: '#87d068' }} icon="user" />
                }
            },
            {
                title:'',
                key:'sex',
                dataIndex: 'sex',
                render: (sex) => {
                    if (sex === 1) {
                        return <Badge count='' style={{ backgroundColor: '#1890ff' }} />;
                    }
                    return <Badge count='' style={{ backgroundColor: '#52c41a' }} />;
                }
            },
            {
                title:'',
                key:'age',
                dataIndex:'age'
            },
            {
                title:'',
                key:'role',
                dataIndex: 'role',
                render: (role) => {
                    const roleObj = {
                        1:'',
                        2:'',
                        3:'',
                        4:'',
                        5:'',
                    }
                    return roleObj[role];
                }
            },
            {
                title:'',
                key:'interest',
                dataIndex: 'interest',
                render: (interest) => {
                    const interestObj = {
                        1:'',
                        2:'',
                        3:'',
                        4:'',
                        5:'K',
                    }
                    let strArr = [];
                    interest.map((item, index) => {
                        strArr.push(interestObj[item]);
                    })

                    return (<Tooltip title={strArr.length >2? strArr.join(','):''}>
                        <span>{strArr.length === 0?'':strArr.length > 2?strArr.slice(0,2).join(',')+'...':strArr.join(',')}</span>
                    </Tooltip>
                    )
                  
                }
            },
            {
                title:'',
                key:'isMarried',
                dataIndex: 'isMarried',
                render: (isMarried) => {
                    if (isMarried) {
                        return <Badge status="success" text="" />;
                    }
                    return <Badge status="error" text="" />;
                }
            },
            {
                title:'',
                key:'birthday',
                dataIndex: 'birthday',
                render: (birthday) => {
                    return moment(birthday * 1000).format("YYYY-MM-DD HH:mm:ss");
                }
            },
            {
                title:'',
                key:'address',
                dataIndex: 'address',
                render: (address) => {
                    return (<Tooltip title={address.length >10? address:''}>
                        <span>{address.length >10?address.slice(0,10)+'...':address}</span>
                    </Tooltip>
                    )
                }
            },
            {
                title:'',
                key:'time',
                dataIndex:'time'
            }
        ]
        // 
        columns.map(item => {
            item.align = "center";
        })
        const { loading, selectedRowKeys,actionMode } = this.state;
        const rowSelection = {
            type:actionMode,
            selectedRowKeys,
            onChange: this.onSelectChange,
        };
        const hasSelected = selectedRowKeys.length > 0;
        

        return (
            <div>
                <Card title="">
                    <div>
                        <div style={{ marginBottom: 16 }}>
                            <Radio.Group defaultValue={actionMode} buttonStyle="solid" onChange={this.onChangeBtn}>
                                <Radio.Button value="radio"></Radio.Button>
                                <Radio.Button value="checkbox"></Radio.Button>
                            </Radio.Group>
                            <Button
                                style={{marginLeft:8}}
                                type="danger"
                                disabled={!hasSelected}
                                loading={loading}
                                onClick={this.handleDelete}
                            
                            ></Button>
                            <span style={{ marginLeft: 8 }}>
                                {hasSelected?`${selectedRowKeys.length}`:''}
                            </span>
                        </div>

                    <Table
                        size="middle"
                        bordered
                        columns={columns}
                        dataSource={this.state.dataSource}
                        // pagination={false}
                        pagination={this.state.pagination}
                            rowSelection={rowSelection} //
                            onRow={(record,index) => {
                                return {
                                    onClick: () => {
                                        //
                                        if (actionMode === "radio") {
                                            this.onRowClick(record,index);
                                        }
                                    }
                                }
                            }}
                    />
                    </div>
                    

                    
                </Card>
            </div>
        )
    }

    
    


}


