import React from 'react';
import { Card, Form, Select, Button, Table, message, Modal, Radio, notification } from 'antd';
import axios from '../../axios/axios';
import Utils from './../../utils/utils';
import moment from 'moment';


const FormItem = Form.Item;
const Option = Select.Option;

const cityIdArr = [
    { id: '', name: '' },
    { id: 1, name: '' },
    { id: 2, name: '' },
    { id: 3, name: '' },
    { id: 4, name: '' },
    { id: 5, name: '' }
]

const modeArr = [
    { id: '', name: '' },
    { id: 1, name: '' },
    { id: 2, name: '' },
]
const opModeArr = [
    { id: '', name: '' },
    { id: 1, name: '' },
    { id: 2, name: '' },
]
const authStatusArr = [
    { id: '', name: '' },
    { id: 1, name: '' },
    { id: 2, name: '' },
]
export default class City extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isShowOpenCityModal: false,
        };
    }
    params = {
        page: 1,
        pageSize: 10,
    }

    getList() {
        let _this = this;
        axios.ajax({
            url: '/open/city',
            data: { ...this.params }
        }).then((res) => {
            let list = res.result.list.map((item, index) => {
                item.key = index;
                return item;
            });
            this.setState({
                list: list,
                pagination: Utils.pagination(res, (current) => {
                    _this.params.page = current;
                    _this.getList();
                })
            })
        })
    }

    componentDidMount() {
        this.getList();
    }

    //-
    search = (postData) => {
        console.log('postData', postData)
        message.info(`${JSON.stringify(postData)}`)
    }

    //-Modal
    handleOpenCity = () => {
        this.setState(() => {
            return {
                isShowOpenCityModal: true
            }
        })
    }

    //--
    openCitySubmit = () => {
        let cityInfo = this.cityForm.props.form.getFieldsValue();
        message.info(`${JSON.stringify(cityInfo)}`);
        setTimeout(() => {
            this.setState(() => {
                return {
                    isShowOpenCityModal: false
                }
            })
            notification.success({
                message: '',
                description: '',
            })
        }, 3000)
    }

    render() {
        const columns = [
            {
                title: 'ID',
                dataIndex: 'id',
                width: 80,
            },
            {
                title: '',
                dataIndex: 'name',
                width: 135,
            },
            {
                title: '',
                dataIndex: 'mode',
                render: (mode) => {
                    return mode == 1 ? '' : '';
                },
                width: 135,
            },
            {
                title: '',
                dataIndex: 'op_mode',
                render(op_mode) {
                    return op_mode == 1 ? '' : '';
                },
                width: 100,
            },
            {
                title: '',
                dataIndex: 'franchisee_name',
                width: 135,
            },
            {
                title: '',
                dataIndex: 'city_admins',
                render(arr) {
                    return arr.map((item) => {
                        return item.user_name;
                    }).join(',');
                },
                width: 150,
            },
            {
                title: '',
                dataIndex: 'open_time',
                render: (open_time) => {
                    return moment(open_time * 1000).format("YYYY-MM-DD HH:mm:ss");
                },
                width: 200,
            },
            {
                title: '',
                dataIndex: 'update_time',
                render: (update_time) => {
                    return moment(update_time * 1000).format("YYYY-MM-DD HH:mm:ss");
                },
                width: 200,
            },
            {
                title: '',
                dataIndex: 'sys_user_name',
            }
        ]

        columns.map((item, index) => {
            item.align = "center";
        })




        return (
            <div>
                <Card>
                    <FilterForm
                        search={this.search}
                    />
                </Card>
                <Card style={{ marginTop: 20 }}>
                    <Button type="primary" onClick={this.handleOpenCity}></Button>
                </Card>
                <div className="content-wrap">
                    <Table
                        bordered
                        columns={columns}
                        dataSource={this.state.list}
                        pagination={this.state.pagination}
                        scroll={{ y: 350 }}
                    />
                </div>
                <Modal
                    title=""
                    visible={this.state.isShowOpenCityModal}
                    onCancel={() => {
                        this.setState(() => {
                            return {
                                isShowOpenCityModal: false
                            }
                        })
                        this.cityForm.props.form.resetFields();
                    }}
                    onOk={this.openCitySubmit}
                >
                    <OpenCityFormModal wrappedComponentRef={(open) => { this.cityForm = open; }} />
                </Modal>
            </div>
        )
    }
}

class FilterForm extends React.Component {

    constructor(props) {
        super(props);
    }

    //
    search = (e) => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                console.log(values)
                this.props.search(values);
            }
        })
    }

    //
    reset = () => {
        this.props.form.resetFields();
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        return (
            <Form layout="inline">
                <FormItem label="">
                    {
                        getFieldDecorator('city_id', {
                            initialValue: ''
                        })(
                            <Select style={{ width: 135 }}>
                                {
                                    cityIdArr.map((item, index) => {
                                        return <Option key={item.id} value={item.id}>{item.name}</Option>
                                    })
                                }
                            </Select>
                        )
                    }
                </FormItem>
                <FormItem label="">
                    {
                        getFieldDecorator('mode', {
                            initialValue: ''
                        })(
                            <Select style={{ width: 140 }}>
                                {
                                    modeArr.map((item, index) => {
                                        return <Option key={item.id} value={item.id}>{item.name}</Option>
                                    })
                                }
                            </Select>
                        )
                    }
                </FormItem>


                <FormItem label="">
                    {
                        getFieldDecorator('op_mode', {
                            initialValue: ''
                        })(
                            <Select style={{ width: 100 }}>
                                {
                                    opModeArr.map((item, index) => {
                                        return <Option key={item.id} value={item.id}>{item.name}</Option>
                                    })
                                }
                            </Select>
                        )
                    }
                </FormItem>

                <FormItem label="">
                    {
                        getFieldDecorator('auth_status', {
                            initialValue: ''
                        })(
                            <Select style={{ width: 100 }}>
                                {
                                    authStatusArr.map((item, index) => {
                                        return <Option key={item.id} value={item.id}>{item.name}</Option>
                                    })
                                }
                            </Select>
                        )
                    }
                </FormItem>
                <FormItem>
                    <Button onClick={this.search} type="primary" icon="search" style={{ margin: '0 10px' }}></Button>
                    <Button onClick={this.reset}></Button>
                </FormItem>

            </Form>
        )
    }
}

FilterForm = Form.create({})(FilterForm);




class OpenCityFormModal extends React.Component {

    render() {
        const formItemLayout = {
            labelCol: {
                span: 5
            },
            wrapperCol: {
                span: 19
            }
        }
        const { getFieldDecorator } = this.props.form;

        return (
            <Form layout="horizontal">
                <FormItem label="" {...formItemLayout}>
                    {
                        getFieldDecorator('city_id', {
                            initialValue: 1,
                        })(
                            <Select style={{ width: 100 }}>
                                {
                                    cityIdArr.filter((item, index) => index > 0).map((item) => {
                                        return <Option key={item.id} value={item.id}>{item.name}</Option>
                                    })
                                }
                            </Select>
                        )
                    }
                </FormItem>

                <FormItem label="" {...formItemLayout}>
                    {
                        getFieldDecorator('op_mode', {
                            initialValue: 1,
                        })(
                            <Radio.Group>
                                {
                                    opModeArr.filter((item, index) => index > 0).map((item) => {
                                        return <Radio key={item.id} value={item.id}>{item.name}</Radio>
                                    })
                                }

                            </Radio.Group>
                        )
                    }
                </FormItem>

                <FormItem label="" {...formItemLayout}>
                    {
                        getFieldDecorator('use_mode', {
                            initialValue: 1,
                        })(
                            <Radio.Group>
                                {
                                    modeArr.filter((item, index) => index > 0).map((item) => {
                                        return <Radio key={item.id} value={item.id}>{item.name}</Radio>
                                    })
                                }

                            </Radio.Group>
                        )
                    }
                </FormItem>

            </Form>
        );
    }
}

OpenCityFormModal = Form.create({})(OpenCityFormModal);