import React from 'react';
import { Card,Button,Icon,Radio } from 'antd';


import './ui.less';


export default class Buttons extends React.Component{
    constructor(){
        super();
        this.state={
            loading:true,
            size:'default'
        }
    }
    //loading
    handleCloseLoading = ()=>{
        this.setState({
            loading:false
        })
    }
    //
    handleChangeSize = (e) => {
        this.setState({
            size:e.target.value
        })
    }
    render(){
        return (
            <div>
                <Card title="" className="card-wrap">
                    <Button>default</Button>
                    <Button type="primary">primary</Button>
                    <Button type="dashed">dashed</Button>
                    <Button type="danger">danger</Button>
                    <Button disabled>disabled</Button>
                </Card>
                <Card title="" className="card-wrap">
                    <Button icon="plus"></Button>
                    <Button icon="edit"></Button>
                    <Button icon="delete"></Button>
                    <Button icon="search" shape="circle"></Button>
                    <Button icon="search" type="primary"></Button>
                    <Button icon="download" type="primary"></Button>
                    <Button icon="download" type="primary" shape="round"></Button>
                </Card>
                <Card title="Loading" className="card-wrap">
                    <Button loading={this.state.loading}></Button>
                    <Button loading={this.state.loading}  type="primary" shape="circle"></Button>
                    <Button loading={this.state.loading}></Button>
                    <Button loading={this.state.loading} shape="circle"></Button>
                    <Button type="primary" onClick={this.handleCloseLoading}></Button>
                </Card>
                <Card title="" style={{marginBottom:10}}>
                    <Button.Group>
                        <Button type="primary" icon="left"></Button>
                        <Button type="primary"><Icon type="right" /></Button>
                    </Button.Group>
                </Card>

                <Card title="" className="card-wrap">
                    <Radio.Group onChange={this.handleChangeSize} value={this.state.size}>
                        <Radio value="small"></Radio>
                        <Radio value="default"></Radio>
                        <Radio value="large"></Radio>
                    </Radio.Group>
                    <Button size={this.state.size}>default</Button>
                    <Button size={this.state.size} type="primary">primary</Button>
                    <Button size={this.state.size} type="dashed">dashed</Button>
                    <Button size={this.state.size} type="danger">danger</Button>
                    <Button size={this.state.size} disabled>disabled</Button>
                </Card>
            </div>
            
        );
    }
}