import React from 'react';
import { Card, Form, Input, Button, Icon, message, Radio, InputNumber, Select, Switch, DatePicker, TimePicker, Cascader, Upload, Checkbox } from 'antd';
import moment from 'moment';

class Register extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            userImg: '',//
            loading:false,
        }
    }



    handleSubmit = () => {
        // let values = this.props.form.getFieldsValue();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                console.log(JSON.stringify(values,null,2));
                message.success(`${values.userName}${values.password}`)
            }
        })
    }

    //
    displayRender = (label) => {
        console.log(label)
        return label.join('-');
    }

    // 
    beforeUpload = (file)=>{
        const isJPG = file.type === 'image/jpeg';
        if (!isJPG) {
            message.error('jpg');
        }

        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!isLt2M) {
            message.error('2MB!');
        }
        return isJPG && isLt2M;
    }

    getBase64 = (img,callback) => {
        const reader = new FileReader();
        reader.addEventListener('load', () => callback(reader.result));
        reader.readAsDataURL(img);
    }

    handleChange = (info) => {
        if (info.file.status === 'uploading') {
            this.setState({ loading: true });
            return;
        }
        if (info.file.status === 'done') {
            // Get this url from response in real world.
            this.getBase64(info.file.originFileObj, imageUrl => this.setState({
                userImg:imageUrl,
                loading: false,
            }));
        }
    }

    render() {
        const { getFieldDecorator } = this.props.form;

        const formItemLayout = {
            labelCol: {
                xs: 24,
                sm: 3
            },
            wrapperCol: {
                xs: 24,
                sm: 12
            }
        }
        const offsetLayout = {
            wrapperCol: {
                xs: {
                    span: 24
                },
                sm: {
                    span: 12,
                    offset: 3
                }
            }
        }
        const roleSelectList = [
            { id: 1, value: '' },
            { id: 2, value: '' },
            { id: 3, value: '' },
            { id: 4, value: '' },
            { id: 5, value: '' }
        ]
        const interestSelectList = [
            { id: 1, value: '' },
            { id: 2, value: '' },
            { id: 3, value: '' },
            { id: 4, value: '' },
            { id: 5, value: 'K' }
        ]
        const options = [
            {
                value: '',//
                label: '',//
                children: [
                    {
                        value: '',
                        label: '',
                        children: [
                            {
                                value: '',
                                label: '',
                            },
                            {
                                value: '',
                                label: '',
                            },
                            {
                                value: '',
                                label: '',
                            },
                            {
                                value: '',
                                label: '',
                            }
                        ]
                    },
                    {
                        value: '',
                        label: '',
                        children: [
                            {
                                value: '',
                                label: '',
                            },
                            {
                                value: '',
                                label: '',
                            },
                            {
                                value: '',
                                label: '',
                            },
                            {
                                value: '',
                                label: '',
                            }

                        ]
                    }
                ]
            },
            {
                value: '',
                label: '',
                children: [
                    {
                        value: '',
                        label: '',
                        children: [
                            {
                                vlaue:'',
                                label:'',
                            },
                            {
                                vlaue:'',
                                label:'',
                            },
                            {
                                vlaue:'',
                                label:'',
                            }
                       
                        ]
                    },
                    {
                        value: '',
                        label: '',
                    },
                    {
                        value: '',
                        label: '',
                    },
                    {
                        value: '',
                        label: '',
                    }
                ]
            }
        ]

        const uploadButton = (
            <div>
                <Icon type={this.state.loading ? 'loading' : 'plus'} />
                <div className="ant-upload-text"></div>
            </div>
        );
        return (
            <div>
                <Card title="">
                    <Form layout="horizontal">
                        <Form.Item label="" {...formItemLayout}>
                            {
                                getFieldDecorator('userName', {
                                    initialValue: '',
                                    rules: [
                                        {
                                            required: true,
                                            message: ''
                                        },
                                        {
                                            min: 5, max: 12,
                                            message: '5-12'
                                        }, {
                                            pattern: new RegExp('^\\w+$', 'g'),
                                            message: ''
                                        }
                                    ]
                                })(
                                    <Input prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />} placeholder="" />
                                )
                            }

                        </Form.Item>

                        <Form.Item label="" {...formItemLayout}>
                            {
                                getFieldDecorator('password', {
                                    initialValue: '',
                                    rules: [
                                        {
                                            required: true,
                                            message: ''
                                        },
                                        {
                                            pattern: new RegExp(/^[A-Za-z0-9~`!@#$%^&*()_+-='",;.?/|]{6,20}$/, 'g'),
                                            message: '6-20()'
                                        }
                                    ]
                                })(
                                    <Input type="password" prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />} placeholder="" />
                                )
                            }

                        </Form.Item>

                        <Form.Item label="" {...formItemLayout}>
                            {
                                getFieldDecorator('sex', {
                                    initialValue: 1,
                                })(
                                    <Radio.Group>
                                        <Radio value={1}></Radio>
                                        <Radio value={2}></Radio>
                                    </Radio.Group>
                                )
                            }
                        </Form.Item>

                        <Form.Item label="" {...formItemLayout}>
                            {
                                getFieldDecorator('age', {
                                    initialValue: 23,
                                })(
                                    <InputNumber min={18} max={60} />
                                )
                            }
                        </Form.Item>

                        <Form.Item label="" {...formItemLayout}>
                            {
                                getFieldDecorator('role', {
                                    initialValue: 3,
                                })(
                                    <Select>
                                        {
                                            roleSelectList.map((item, index) => {
                                                return (
                                                    <Select.Option key={item.id} value={item.id}>{item.value}</Select.Option>
                                                )
                                            })
                                        }

                                    </Select>
                                )
                            }
                        </Form.Item>

                        <Form.Item label="" {...formItemLayout}>
                            {
                                getFieldDecorator('interest', {
                                    initialValue: [2, 3, 4],
                                })(
                                    <Select mode="multiple" placeholder="">
                                        {
                                            interestSelectList.map((item, index) => {
                                                return (
                                                    <Select.Option key={item.id} value={item.id}>{item.value}</Select.Option>
                                                )
                                            })
                                        }

                                    </Select>
                                )
                            }
                        </Form.Item>

                        <Form.Item label="" {...formItemLayout}>
                            {
                                getFieldDecorator('isMarried', {
                                    initialValue: false,
                                })(
                                    <Switch checkedChildren="" unCheckedChildren="" />
                                )
                            }
                        </Form.Item>

                        <Form.Item label="" {...formItemLayout}>
                            {
                                getFieldDecorator('birthday', {
                                    initialValue: moment('1993-06-06'),
                                })(
                                    <DatePicker showTime format="YYYY-MM-DD HH:mm:ss" />
                                )
                            }
                        </Form.Item>


                        <Form.Item label="" {...formItemLayout}>
                            {
                                getFieldDecorator('address', {
                                    initialValue: '',
                                })(
                                    <Input.TextArea autosize={{
                                        minRows: 4, maxRows: 6
                                    }}></Input.TextArea>
                                )
                            }
                        </Form.Item>

                        <Form.Item label="" {...formItemLayout}>
                            {
                                getFieldDecorator('addressSelect', {
                                    initialValue: ['','',''],
                                })(
                                    <Cascader
                                        options={options}
                                        expandTrigger="hover"
                                        displayRender={this.displayRender}
                                    />
                                )
                            }
                        </Form.Item>

                        <Form.Item label="" {...formItemLayout}>
                            {
                                getFieldDecorator('time', {
                                    initialValue: moment('08-10-00',"HH:mm:ss"),
                                })(
                                    <TimePicker />
                                )
                            }
                        </Form.Item>

                        <Form.Item label="" {...formItemLayout}>
                            {
                                getFieldDecorator('userImg', {
                                    rules: [
                                        {
                                            required: true,
                                            message:''
                                        }
                                    ]
                                })(
                                    <Upload
                                        listType="picture-card"
                                        showUploadList={false}
                                        action="//jsonplaceholder.typicode.com/posts/"
                                        beforeUpload={this.beforeUpload}
                                        onChange={this.handleChange}
                                    >
                                    {this.state.userImg?<img src={this.state.userImg} alt=""/>:uploadButton}
                                    </Upload>
                                )
                            }
                        </Form.Item>



                        <Form.Item {...offsetLayout}>
                            {
                                getFieldDecorator('isAgree', {
                                    initialValue: true,
                                    valuePropName:'checked'
                                })(
                                    <Checkbox><a></a> </Checkbox>
                                )
                            }
                        </Form.Item>



                        <Form.Item {...offsetLayout}>
                            <Button type="primary" htmlType="submit" onClick={this.handleSubmit}></Button>
                        </Form.Item>

                    </Form>
                </Card>
            </div>
        )
    }
}

export default Form.create()(Register);

