import React from 'react';
import { Card,Button,Modal } from 'antd';


import './ui.less';


export default class Modals extends React.Component{
    constructor(){
        super();
        this.state={
            visible0: false,
            visible1: false,
            visible2: false,
            visible3: false,
            visible4: false,
            ModalText:"5s",
            visible4confirmLoading:false,
        }
    }

    openBaseModal = (visible) => {
        this.setState({
            [visible]:true
        })        
    }

    handleConfirm = (type) => {
        Modal[type]({
            title: '',
            content: '',
            okText: '',
            cancelText: '',
            onOk() {
                console.log('ok')
            },
            onCancel() {
                console.log('cancel')
            }
        })
    }
    
    render(){
        return (
            <div>
                <Card title="" className="card-wrap">
                    <Button type="primary" onClick={()=> this.openBaseModal('visible0')}>Open</Button>
                    <Button type="primary" onClick={()=> this.openBaseModal('visible1')}></Button>
                    <Button type="primary" onClick={()=> this.openBaseModal('visible2')}>20px</Button>
                    <Button type="primary" onClick={() => this.openBaseModal('visible3')}></Button>
                    <Button type="primary" onClick={() => this.openBaseModal('visible4')}>5s</Button>
                    
                    
                </Card>
                <Card title="" className="card-wrap">
                    <Button type="primary" onClick={()=>this.handleConfirm('confirm')}>Confirm</Button>
                    <Button type="primary" onClick={()=>this.handleConfirm('info')}>Info</Button>
                    <Button type="primary" onClick={()=>this.handleConfirm('success')}>Success</Button>
                    <Button type="primary" onClick={()=>this.handleConfirm('error')}>Error</Button>
                    <Button type="primary" onClick={()=>this.handleConfirm('warning')}>Warning</Button>
                </Card>

                <Modal 
                    title=""
                    visible={this.state.visible0}
                    onOk={() => {
                        this.setState({
                            visible0:false
                        })
                    }}
                    onCancel={() => {
                        this.setState({
                            visible0:false
                        })
                    }}>
                    <p></p>
                </Modal>

                
                <Modal 
                    title=""
                    visible={this.state.visible1}
                    okText=""
                    cancelText=""
                    onOk={() => {
                        this.setState({
                            visible1:false
                        })
                    }}
                    onCancel={() => {
                        this.setState({
                            visible1:false
                        })
                    }}>
                    <p>Modal okText="" cancelText=""</p>
                </Modal>

                <Modal 
                    title="20px"
                    visible={this.state.visible2}
                    okText="ok"
                    cancelText="cancel"
                    style={{top:20}}
                    onOk={() => {
                        this.setState({
                            visible2:false
                        })
                    }}
                    onCancel={() => {
                        this.setState({
                            visible2:false
                        })
                    }}>
                    <p>20px - Modal style top:20</p>
                </Modal>

                <Modal 
                    title=""
                    visible={this.state.visible3}
                    okText="ok"
                    cancelText="cancel"
                    centered
                    onOk={() => {
                        this.setState({
                            visible3:false
                        })
                    }}
                    onCancel={() => {
                        this.setState({
                            visible3:false
                        })
                    }}>
                    <p> - Modalcentered</p>
                </Modal>

                
                <Modal 
                    title=""
                    visible={this.state.visible4}
                    okText=""
                    cancelText=""
                    centered
                    confirmLoading={this.state.visible4confirmLoading}
                    onOk={() => {
                        this.setState({
                            ModalText: '5s-5s',
                            visible4confirmLoading: true,
                        });

                        setTimeout(()=>{
                            this.setState({
                                visible4:false,
                                visible4confirmLoading: false,
                            });
                        },5000)

                    }}
                    onCancel={() => {
                        this.setState({
                            visible4:false
                        })
                    }}>
                    <p>{this.state.ModalText}</p>
                </Modal>

            </div>
            
        );
    }
}