import React from 'react';
import { Card, Table, Avatar, Badge, Tooltip, Button, Radio, Modal, notification, Form, Switch } from 'antd';
import moment from 'moment';
import axios from '../../axios/axios';
import Utils from '../../utils/utils';
const FormItem = Form.Item;

const scrollY = { y: 300 };
const scrollX = { x: 1300 };

export default class HightTable extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            dataSource: [],
            selectedRowKeys: [],
            loading: false,
            scrollY: undefined,
            scrollX: undefined,
        }
    }

    params = {
        page: 1,
        pageSize: 10
    }



    //
    handleDelete = () => {
        Modal.confirm({
            title: '',
            content: '',
            okText: '',
            cancelText: '',
            onOk: () => {
                console.log(this.state.selectedRowKeys);
                console.log(this.state.dataSource);
                notification.success({
                    message: '',
                    description: '',
                    duration: 3,//3s 4.5snull
                })
            },
        })
    }


    getList = () => {
        axios.ajax({
            url: '/user/list',
            params: this.params,
        }).then((res) => {
            console.log('res', res);
            let _this = this;
            if (res.code === 0) {
                res.result.list.map((item, index) => {
                    item.key = index;
                })
                console.log(res)
                this.setState({
                    dataSource: res.result.list,
                    selectedRowKeys: [],
                    selectedRows: null,
                    pagination: Utils.pagination(res, (page, pageSize) => {
                        _this.params.page = page;
                        _this.params.pageSize = pageSize;
                        this.getList();
                    })
                })
            }
        })
    }

    //-->render--->-Ajax
    componentDidMount() {
        this.getList();
    }

    //
    handleScollYChange = (enable) => {
        this.setState({
            scrollY: enable ? scrollY : undefined
        })
    }

    handleScollXChange = (enable) => {
        this.setState({
            scrollX: enable ? scrollX : undefined
        })
    }


    render() {
        const columns1 = [
            {
                title: 'id',
                key: 'id',
                dataIndex: 'id',
            },
            {
                title: '',
                key: 'userName',
                dataIndex: 'userName',
            },
            {
                title: '',
                key: 'userImg',
                dataIndex: 'userImg',
                render: (userImg) => {
                    return userImg ? <Avatar src={userImg} /> : <Avatar style={{ backgroundColor: '#87d068' }} icon="user" />
                }
            },
            {
                title: '',
                key: 'sex',
                dataIndex: 'sex',
                render: (sex) => {
                    if (sex === 1) {
                        return <Badge count='' style={{ backgroundColor: '#1890ff' }} />;
                    }
                    return <Badge count='' style={{ backgroundColor: '#52c41a' }} />;
                }
            },
            {
                title: '',
                key: 'age',
                dataIndex: 'age',
                sorter: (a, b) => a.age - b.age
            },
            {
                title: '',
                key: 'role',
                dataIndex: 'role',
                render: (role) => {
                    const roleObj = {
                        1: '',
                        2: '',
                        3: '',
                        4: '',
                        5: '',
                    }
                    return roleObj[role];
                }
            },
            {
                title: '',
                key: 'interest',
                dataIndex: 'interest',
                render: (interest) => {
                    const interestObj = {
                        1: '',
                        2: '',
                        3: '',
                        4: '',
                        5: 'K',
                    }
                    let strArr = [];
                    interest.map((item, index) => {
                        strArr.push(interestObj[item]);
                    })

                    return (<Tooltip title={strArr.length > 2 ? strArr.join(',') : ''}>
                        <span>{strArr.length === 0 ? '' : strArr.length > 2 ? strArr.slice(0, 2).join(',') + '...' : strArr.join(',')}</span>
                    </Tooltip>
                    )

                }
            },
            {
                title: '',
                key: 'isMarried',
                dataIndex: 'isMarried',
                render: (isMarried) => {
                    if (isMarried) {
                        return <Badge status="success" text="" />;
                    }
                    return <Badge status="error" text="" />;
                }
            },
            {
                title: '',
                key: 'birthday',
                dataIndex: 'birthday',
                render: (birthday) => {
                    return moment(birthday * 1000).format("YYYY-MM-DD HH:mm:ss");
                }
            },
            {
                title: '',
                key: 'address',
                dataIndex: 'address',
                render: (address) => {
                    return (<Tooltip title={address.length > 10 ? address : ''}>
                        <span>{address.length > 10 ? address.slice(0, 10) + '...' : address}</span>
                    </Tooltip>
                    )
                }
            },
            {
                title: '',
                key: 'time',
                dataIndex: 'time',
            },
            {
                title: '',
                key: 'action',
                dataIndex: 'action',
                render: () => {
                    return (
                        <div>
                            <Button size="small" type="primary"></Button>
                            <Button onClick={this.handleDelete} size="small" type="danger"></Button>
                            <Button size="small" type="default"></Button>
                            <Button size="small" type="dashed"></Button>
                        </div>
                    )
                }
            },

        ]

        columns1.map((item,index) => {
            item.align = "center";
            item.width = 80
            if (index === columns1.length - 1) {
                item.width = 200
            }
        })



        const columns2 = [
            {
                title: 'id',
                key: 'id',
                dataIndex: 'id',
                fixed: 'left',
            },
            {
                title: '',
                key: 'userName',
                dataIndex: 'userName',
            },
            {
                title: '',
                key: 'userImg',
                dataIndex: 'userImg',
                render: (userImg) => {
                    return userImg ? <Avatar src={userImg} /> : <Avatar style={{ backgroundColor: '#87d068' }} icon="user" />
                }
            },
            {
                title: '',
                key: 'sex',
                dataIndex: 'sex',
                render: (sex) => {
                    if (sex === 1) {
                        return <Badge count='' style={{ backgroundColor: '#1890ff' }} />;
                    }
                    return <Badge count='' style={{ backgroundColor: '#52c41a' }} />;
                }
            },
            {
                title: '',
                key: 'age',
                dataIndex: 'age'
            },
            {
                title: '',
                key: 'role',
                dataIndex: 'role',
                render: (role) => {
                    const roleObj = {
                        1: '',
                        2: '',
                        3: '',
                        4: '',
                        5: '',
                    }
                    return roleObj[role];
                }
            },
            {
                title: '',
                key: 'interest',
                dataIndex: 'interest',
                render: (interest) => {
                    const interestObj = {
                        1: '',
                        2: '',
                        3: '',
                        4: '',
                        5: 'K',
                    }
                    let strArr = [];
                    interest.map((item, index) => {
                        strArr.push(interestObj[item]);
                    })

                    return (<Tooltip title={strArr.length > 2 ? strArr.join(',') : ''}>
                        <span>{strArr.length === 0 ? '' : strArr.length > 2 ? strArr.slice(0, 2).join(',') + '...' : strArr.join(',')}</span>
                    </Tooltip>
                    )

                }
            },
            {
                title: '',
                key: 'isMarried',
                dataIndex: 'isMarried',
                render: (isMarried) => {
                    if (isMarried) {
                        return <Badge status="success" text="" />;
                    }
                    return <Badge status="error" text="" />;
                }
            },
            {
                title: '',
                key: 'birthday',
                dataIndex: 'birthday',
                render: (birthday) => {
                    return moment(birthday * 1000).format("YYYY-MM-DD HH:mm:ss");
                }
            },
            {
                title: '',
                key: 'address',
                dataIndex: 'address',
                render: (address) => {
                    return (<Tooltip title={address.length > 10 ? address : ''}>
                        <span>{address.length > 10 ? address.slice(0, 10) + '...' : address}</span>
                    </Tooltip>
                    )
                }
            },
            {
                title: '',
                key: 'time',
                dataIndex: 'time',
            },
            {
                title: '',
                key: 'time',
                dataIndex: 'time',
            },
            {
                title: '',
                key: 'time',
                dataIndex: 'time',
            },
            {
                title: '',
                key: 'time',
                dataIndex: 'time',
            },
            {
                title: '',
                key: 'time',
                dataIndex: 'time',
            },
            {
                title: '',
                key: 'time',
                dataIndex: 'time',
                fixed: 'right',
            }
        ]

       


        // 
        columns2.map(item => {
            item.align = "center";
            item.width = 80
        })
        const { loading, selectedRowKeys } = this.state;

        const hasSelected = selectedRowKeys.length > 0;


        return (
            <div>
                <Card title="---">
                    <div>
                        <div style={{ marginBottom: 16 }}>
                            <Form layout="inline">
                                <FormItem label="">
                                    <Switch checkedChildren="" unCheckedChildren="" checked={!!this.state.scrollY} onChange={this.handleScollYChange} />
                                </FormItem>
                            </Form>
                            <Button
                                style={{ marginLeft: 8 }}
                                type="danger"
                                disabled={!hasSelected}
                                loading={loading}
                                onClick={this.handleDelete}

                            ></Button>
                            <span style={{ marginLeft: 8 }}>
                                {hasSelected ? `${selectedRowKeys.length}` : ''}
                            </span>
                        </div>

                        <Table
                            size="middle"
                            bordered
                            columns={columns1}
                            dataSource={this.state.dataSource}
                            // pagination={false}
                            pagination={this.state.pagination}
                            scroll={this.state.scrollY}
                        />
                    </div>



                </Card>

                <Card title="-">
                    <div>
                        <div style={{ marginBottom: 16 }}>
                            <Form layout="inline">
                                <FormItem label="">
                                    <Switch checkedChildren="" unCheckedChildren="" checked={!!this.state.scrollX} onChange={this.handleScollXChange} />
                                </FormItem>


                            </Form>
                            <Button
                                style={{ marginLeft: 8 }}
                                type="danger"
                                disabled={!hasSelected}
                                loading={loading}
                                onClick={this.handleDelete}

                            ></Button>
                            <span style={{ marginLeft: 8 }}>
                                {hasSelected ? `${selectedRowKeys.length}` : ''}
                            </span>
                        </div>

                        <Table
                            size="middle"
                            bordered
                            columns={columns2}
                            dataSource={this.state.dataSource}
                            // pagination={false}
                            pagination={this.state.pagination}
                            scroll={this.state.scrollX}
                        />
                    </div>



                </Card>
            </div>
        )
    }





}


