import React from 'react';
import { Card,Spin,Radio, Icon,Alert,Switch } from 'antd';
import './ui.less';

export default class Loadings extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            size: 'default',
            loading:true,
        }
    }

    handleChangeSize = (e)=>{
        this.setState({
            size:e.target.value
        })
    }

    handleChangeLoading = (bool) => {
        this.setState({
            loading:bool
        })
    }

    render() {
        const icon = <Icon type="loading"></Icon>
        return (
            <div>
               
                <Card title="spin" className="card-wrap">
                    <Radio.Group value={this.state.size} onChange={this.handleChangeSize}>
                        <Radio value="small"></Radio>
                        <Radio value="default"></Radio>
                        <Radio value="large"></Radio>
                    </Radio.Group>
                    <Spin size={this.state.size}></Spin>
                    <Spin size={this.state.size} indicator={icon} style={{ marginLeft: 20 }}></Spin>
                </Card>

                <Card title="Spin + Alert" className="card-wrap">
                   
                    <div style={{marginBottom:20}}>
                        Loading state: <Switch checked={this.state.loading} onChange={this.handleChangeLoading}/>
                    </div>
                    <Alert
                        showIcon
                        closable
                        onClose={() => {
                            alert('');
                        }}
                        message="Alert type='success'"
                        description=""
                        type="success"
                        style={{marginBottom:20}}
                    />
                    
                    <Spin size={this.state.size} spinning={this.state.loading}>
                        <Alert
                            message="Alert type='info'"
                            description="html/css/js egmy_project_name"
                            type="info"
                            style={{marginBottom:20}}
                        />
                    </Spin>

                    <Spin size={this.state.size} tip="..." spinning={this.state.loading}>
                        <Alert
                            message="Alert type='warning' Spin tip='...' "
                            description="html/css/js.eg: login.htmllogin.csslogin.js"
                            type="warning"
                            style={{marginBottom:20}}
                        />
                    </Spin>

                    <Spin size={this.state.size} indicator={icon} spinning={this.state.loading}>
                        <Alert
                            message="Alert type='error' indicator='<Icon type='loading'></Icon>' "
                            description="classegbtn-playbtn_play"
                            type="error"
                        />
                    </Spin>

                </Card>

            </div>
        )
    }

    


}


