import React from 'react';

import { Card,Form,Icon,Input,Button, Checkbox } from 'antd';



function hasErrors(fieldsError) {
    return Object.keys(fieldsError).some(field => fieldsError[field]);
}

  

class Login extends React.Component {
    constructor(props) {
        super(props);
        this.state = {

        }
    }

    componentDidMount() {
        this.props.form.validateFields();
    }

    handleSubmit = (e) => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                console.log(values)
            }
        })
    }

    handleSubmit2 = (e) => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                console.log(values)
            }
        })
    }
    

   
    render() {
        // console.log(this.props.form);
        const { isFieldTouched, getFieldError,getFieldDecorator,getFieldsError } = this.props.form;
        const userNameError = isFieldTouched('userName') && getFieldError('userName');
        const passwordError = isFieldTouched('password') && getFieldError('password');

        return (
            <div>
                <Card title="">
                    <Form layout="inline" onSubmit={this.handleSubmit}>
                        <Form.Item
                            validateStatus={userNameError ? "error" : ""}
                            help={userNameError || ""}
                        >
                            {
                                getFieldDecorator('userName', {
                                    rules: [
                                        {
                                            required:true,
                                            message:''
                                        },
                                        {
                                            min:5,max:10,
                                            message:'5-10'
                                        },
                                        {
                                            pattern:new RegExp('^\\w+$','g'),
                                            message:''
                                        }
                                    ]
                                })(
                                    <Input prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }}/>} placeholder=""/>
                                )
                            }
                        </Form.Item>
                        <Form.Item
                            validateStatus={passwordError ? "error" : ''}
                            help={passwordError || ''}
                        >
                            {
                                getFieldDecorator('password',{
                                    rules:[
                                        {
                                            required:true,
                                            message:''
                                        },
                                        // {
                                        //     min:6,max:20,
                                        //     message:'6-20'
                                        // },
                                        {
                                            pattern:new RegExp(/^[A-Za-z0-9~`!@#$%^&*()_+-='",;.?/|]{6,20}$/,'g'),
                                            message:'6-20()'
                                        }
                                    ]
                                })(
                                    <Input prefx={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }}/>} placeholder=""/>
                                )
                            }
                        </Form.Item>
                        <Form.Item>
                            <Button type="primary" htmlType="submit" disabled={hasErrors(getFieldsError())}></Button>
                        </Form.Item>
                    </Form>
                </Card>
            
            
                <Card title="" style={{marginTop:20}}>
                    <Form style={{width:300}}>
                        <Form.Item>
                            {
                                getFieldDecorator('userName',{
                                    initialValue:'',
                                    rules:[
                                        {
                                            required:true,
                                            message:''
                                        },
                                        {
                                            min:5,max:10,
                                            message:'5-10'
                                        },
                                        {
                                            pattern:new RegExp('^\\w+$','g'),
                                            message:''
                                        }
                                    ]
                                })(
                                    <Input prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }}/>} placeholder=""/>
                                )
                            }
                        </Form.Item>
                        <Form.Item>
                            {
                                getFieldDecorator('password', {
                                    initialValue: '',
                                    rules: []
                                })(
                                    <Input prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }}/>} placeholder=""/>
                                )
                            }
                        </Form.Item>
                        <Form.Item>
                            {
                                getFieldDecorator('remember', {
                                    initialValue: true,
                                    valuePropName:'checked'
                                })(
                                    <Checkbox></Checkbox>
                                )
                            }
                            <a href="#" style={{float:'right'}}></a>
                        </Form.Item>
                        <Form.Item>
                            <Button onClick={this.handleSubmit2} type="primary" htmlType="submit" style={{width:'100%'}}></Button>
                        </Form.Item>
                    </Form>
                </Card>
            </div>
        )
    }

}

export default Form.create()(Login);


